@import '../../styles/mixins';

.headersPage {
  max-height: 200px;
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 1000;
  padding-bottom: 70px;
  @media screen and (max-width: 600px) {
    flex-direction: row;
  };
  .headersLogo {
    width: 130px;
    align-self: center;
    @media screen and (max-width: 600px) {
      align-self: start;
      padding-left: 15px;
      height: 30px;
    };
  }

  .headersLinks {
    @media screen and (max-width: 600px) {
      display: flex;
      flex-direction: column;


    };

  }

  .headersLinks a {
    text-decoration: none;
    margin-left: 5px;
    cursor: pointer;
    padding: 5px 15px;
    border-radius: 13px;
    transition: 0.7s;
    font-family: Montserrat;

    &:hover {
      color: #f39d3e;

    }

    @include text(500, 18px, 110%, #101010);
    @media screen and (max-width: 420px) {
      font-size: 14px;
    };

    .headersLink a.active {
      color: #000;
    }

  }
}
